<template>
  <div class="box">
    <h3 class="header">
      <span>医院等级</span>
      <el-button type="primary" @click="add" >新增医院</el-button>
    </h3>
    <el-table :data="comInfoList">
        <el-table-column prop="id" label="ID">
        </el-table-column>
        <el-table-column prop="comName" label="医院等级名称">
        </el-table-column>
        <el-table-column label="操作" width='220'>
          <template slot-scope="scope">
            <el-button @click="handleClick(scope.row)" type="text" size="small">编辑</el-button>
            <el-button type="text" size="small" @click="serveClick">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    <add-book :title="title" :dialogVisible.sync="dialogVisible" :label="label" v-on:control="control" :input="input"></add-book>
    <div class="demonstration-box">
        <!-- <span class="demonstration">显示总数</span> -->
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="100"
          layout="total, prev, pager, next"
          :total="1000">
        </el-pagination>
    </div>
  </div>
</template>

<script>
import addBook from '../../components/addBook'
const comInfo = {
  id: '001',
  comName: '平安',
  address: '南大街',
}
export default {
  name: 'hosLevel',
  data() {
    return {
      title: '新增医院等级',
      label: '请输入等级名称',
      input: '',
      dialogVisible: false,
      comInfoList: Array(6).fill(comInfo),
      currentPage: 1
    }
  },
  components: {
    addBook
  },
  methods: {
    add() {
      this.title = '新增医院等级';
      this.dialogVisible = true;
    },
    control(val) {
      console.log(val)
      this.dialogVisible = val;
    },
    handleSizeChange(val) {
        console.log(`每页 ${val} 条`);
      },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
    },
    handleClick(row) {
      console.log(row);
      this.input = row.comName;
      this.title = '编辑医院等级';
      this.dialogVisible = true;
    },
    serveClick() {
      this.$confirm('此操作将删除该行信息, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });
    }
  }
}
</script>

<style lang="less" scoped>
.box{
  padding-left: 20px;
}
.header{
  border-bottom: 1px solid #eee;
  padding-bottom: 5px;
  display: flex;
  justify-content: space-between
}
</style>
